*{
  font-family: Arial, Helvetica, sans-serif;
  
}

body{
  padding: 60px;
}

input{
  color: grey;
  font-size: 1.2rem;
  width: 100%; 
}

input:focus{
  outline: none;
}

.card-grid{
  display: flex;
  flex-flow: row wrap;

}

.card{
    align-content: center;
    border: 1px solid gray;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}